@import '../../../assets/css/theme';
@import '../../../assets/css/var';

.info_box {
  // background: $color-info-bg;
  // border-radius: 10px;
  // border: 1px solid $color-border-secondary;
  // display: flex;
  // align-items: center;
  // padding: 24px;
  display: flex;
  background: #33254e;
  border-radius: 6px;
  padding: 14px 0;
  &_v {
    flex-direction: column;
  }
  &_no_border {
    border: none;
  }
  &_item {
    flex: 1;
    text-align: center;
    position: relative;
    // display: flex;
    // margin-bottom: 16px;
    border-right: 1px solid rgba(255, 255, 255, 0.1);
    &:last-child {
      border-right: 0;
    }
    // &:after {
    //   width: 1px;
    //   height: 80%;
    //   top: 50%;
    //   transform: translateY(-50%);
    //   position: absolute;
    //   right: 0;
    //   content: '';
    //   background-color: $color-border-secondary;
    // }
  }
  // &_item:last-child {
  //   &:after {
  //     display: none;
  //   }
  // }
  &_label {
    font-family: Poppins;
    font-weight: 400;
    font-size: 14px;
    line-height: 30px;
    color: #a29cbc;
  }
  &_value {
    // flex: 1;
    // width: 0;
    margin-top: 10px;
    font-family: Poppins-Bold;
    font-weight: 700;
    font-size: 16px;
    line-height: 24px;
    // text-align: right;
    text-align: center;
    word-break: break-all;
    color: #fff;
  }
}
.info_box_v {
  padding: 0;
  background: transparent;
  .info_box_item {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 16px;
    border: 0;
    &:last-child {
      margin-bottom: 0;
    }
    &:after {
      display: none;
    }
  }
  .info_box_value {
    margin: 0;
    font-size: 20px;
  }
}
.info_box_v3 {
  padding: 14px 16px;
  background: #33254e;
  border-radius: 6px;
  .info_box_item {
    margin-bottom: 10px;
    &:last-child {
      margin-bottom: 0;
    }
    .info_box_label {
      line-height: 21px;
    }
    .info_box_value {
      font-size: 14px;
      font-weight: 600;
    }
  }
}
.remove_info_box_style {
  display: block;
  padding: 0 20px;
  border-radius: 10px;
  background: #26193d;
  .info_box_item {
    display: flex;
    align-items: center;
    padding: 15px 0;
    border-bottom: 1px solid #33254e;
    border-right: 0;
    &:last-child {
      border-bottom: 0;
    }
    .info_box_label {
      line-height: 20px;
      & > span {
        display: flex;
        align-items: center;
        flex-wrap: nowrap;
      }
    }
    .info_box_value {
      margin: 0 0 0 auto;
      font-family: Poppins-Bold;
      font-weight: 700;
      font-size: 20px;
      line-height: 30px;
      text-align: right;
    }
  }
}
@include mobile-only {
  .info_box {
    &_item {
      margin-bottom: 12px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &_label {
      font-size: 12px;
      line-height: 24px;
    }
    &_value {
      font-size: 16px;
      line-height: 24px;
    }
  }
  .info_box_responsive {
    flex-direction: column;
    justify-content: flex-start;
    .info_box_item {
      text-align: left;
      width: 100%;
      margin-bottom: 18px;
      &:last-child {
        margin-bottom: 0;
      }
      &:after {
        display: none;
      }
    }
    &_h {
      .info_box_item {
        display: flex;
        justify-content: space-between;
        align-items: center;
      }
    }
  }
  .info_box_v {
    .info_box_item {
      margin-bottom: 12px;
    }
    .info_box_value {
      font-size: 16px;
    }
  }
  .info_box_v3 {
    padding: 0 10px;
    .info_box_item {
      display: block;
      margin-bottom: 0;
      padding: 10px 0;
      text-align: left;
      border-bottom: 1px solid rgba(255, 255, 255, 0.1);
      .info_box_label {
        margin-bottom: 6px;
      }
      .info_box_value {
        text-align: left;
      }
    }
  }
  .remove_info_box_style {
    padding: 0 10px;
    border-radius: 6px;
    .info_box_item {
      padding: 13px 0;
      margin-bottom: 0;
      .info_box_label {
        line-height: 20px;
      }
      .info_box_value {
        font-size: 16px;
        line-height: 24px;
      }
    }
  }
}
