@import '../../../assets/css/theme';

.sorter {
  position: relative;
  display: inline-flex;
  height: 25px;
  background: $color-modal-bg;
  border-radius: 5px;
  padding: 6px 10px;
  font-size: 12px;
  align-items: center;
  &_left {
    height: 14px;
    margin-right: 4px;
  }
  &_right {
    height: 7px;
    margin-left: 9px;
  }
  &_list {
    :global {
      .ant-popover-content .ant-popover-inner {
        background: $color-modal-bg;
      }
    }
    ul {
      padding: 0;
      margin: 0;
    }
    padding: 2px 10px;
    border-radius: 5px;
    &_item {
      padding: 8px;
      border-bottom: 1px solid $color-text-default-06;
      font-size: 12px;
      font-family: PingFangSC-Regular, PingFang SC;
      font-weight: 400;
      color: $color-text-default;
      line-height: 17px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      white-space: nowrap;
      img {
        width: 16px;
        margin-left: 8px;
      }
      &:last-child {
        border: none;
      }
      &_active {
        color: $color-a;
      }
    }
  }
}
