@import '../../assets/css/var';
@import '../../assets/css/theme';
@import '../../assets/css/mixins';

.farm-nav-tab {
  padding: 14px 15px 8px 14px;
  @include desktop-only {
    display: none;
  }
  .farm-nav-tab-list {
    display: flex;
    .farm-nav-tab-item {
      position: relative;
      margin-right: 30px;
      height: 28px;
      line-height: 28px;
      font-size: 14px;
      color: $color-text-default-5;
      cursor: pointer;
      user-select: none;
      &:last-child {
        margin-right: 0;
      }
      &::after {
        position: absolute;
        content: '';
        left: 16px;
        right: 16px;
        bottom: -10px;
        height: 4px;
        border-radius: 4px;
      }
    }
    .farm-nav-tab-item-active {
      font-size: 20px;
      color: $color-text-default;
      &::after {
        background: #8c51ff;
      }
    }
  }
}
