@import '../../assets/css/theme';
@import '../../assets/css/var';
@import '../../assets/css/mixins';

.tab_content_main {
  padding: 30px 40px;
  border-radius: 0 0 10px 10px;
  background-color: $color-card-bg-active;
  @include mobile-only {
    padding: 20px 10px 12px;
  }
  :global(.farm_logo) {
    height: 20px;
    margin-right: -5px;
  }
}
.light_with_border {
  @include darkThemeOnly {
    border: none;
  }
  @include lightThemeOnly {
    border: 1px solid $color-border-secondary;
  }
}
.content_title {
  font-size: 20px;
  font-weight: 600;
  color: $color-text-default;
  line-height: 28px;
  display: flex;
  align-items: center;
  margin-bottom: 20px;
  flex-wrap: wrap;
  margin-top: 60px;

  &_left {
    margin-right: auto;
  }
  &_effect {
    font-size: 14px;
    font-weight: 600;
    line-height: 20px;
    margin-right: 30px;
  }
  &_lock_link {
    display: flex;
    align-items: center;
  }
}
.count_down {
  font-size: 14px;
  font-weight: 600;
  line-height: 20px;
  margin-right: 30px;
  &_label {
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
  }
}
.echarts_wrap {
  background-color: $color-info-card-bg;
  border-radius: 10px;
  padding: 20px 0;
  background-image: url('../../assets/images/governance/weight_left_bg.png'),
    url('../../assets/images/governance/weight_right_bg.png');
  background-repeat: no-repeat, no-repeat;
  background-position: left center, right center;
  background-size: contain;
  @include mobile-only {
    background-position: left bottom, right top;
  }
}
.governance_table {
  :global {
    .ant-table-thead > tr th:first-child {
      padding-left: 40px;
    }
    .ant-table-tbody > tr td:first-child {
      padding-left: 40px;
    }
  }
  .pool_logo {
    img {
      height: 20px;
      //&:last-child {
      //  margin-right: 8px;
      //}
      //&:nth-child(2) {
      //  margin-left: -5px;
      //}
    }
  }
}
.vote_form {
  display: flex;
  :global {
    .ant-input-affix-wrapper-status-error:not(.ant-input-affix-wrapper-disabled):not(
        .ant-input-affix-wrapper-borderless
      ).ant-input-affix-wrapper
      .ant-input-suffix {
      color: $color-text-default;
    }
    .ant-form-item {
      &:first-child {
        flex: 1;
        padding-right: 20px;
      }
      .ant-form-item-label label {
        font-family: PingFangSC-Regular, PingFang SC;
        font-weight: 400;
        color: $color-text-default;
      }
    }
    .ant-select-selection-item {
      img {
        height: 20px;
        margin-right: 5px;
      }
    }
    .ant-form-item .ant-form-item-explain-error {
      font-size: 12px;
    }
  }
}
.vote_infoBox {
  > div {
    > div:nth-child(1) {
      font-size: 14px;
    }
    > div:nth-child(2) {
      font-size: 24px;
      margin-top: 20px;
    }
  }
}
.combine_icon {
  img {
    height: 20px;
    margin-right: 5px;
  }
}
.my_vote_table {
  :global {
    .ant-table-header table .ant-table-thead tr > th:first-child {
      padding-left: 20px;
    }
    .ant-table-body table .ant-table-tbody tr > td:first-child {
      padding-left: 20px;
    }
  }
}

@include mobile-only {
  .vote_form {
    display: block;
    :global(.ant-form-item):first-child {
      padding-right: 0;
    }
  }
  .content_title {
    font-size: 16px;
    margin-top: 30px;
    margin-bottom: 14px;
  }
  div.light_with_border {
    padding: 10px 10px 12px;
  }
  div.mobile_vote_info_box {
    padding: 10px 10px 12px;
    > div {
      &:nth-child(2) {
        margin-bottom: 0 !important;
      }
      margin-bottom: 12px !important;
      > div:nth-child(2) {
        font-size: 14px;
        margin-top: 0;
      }
    }
  }
}
.echarts_legend_item {
  margin: 14px auto;
  display: flex;
  align-items: center;
  font-size: 14px;
  font-weight: 600;
  color: $color-text-default;
  line-height: 20px;
  padding-left: 20px;
  .legend_icon {
    width: 12px;
    height: 12px;
    border-radius: 2px;
    border: 1px solid $color-text-default-3;
    margin-right: 8px;
  }
  .legend_num {
    margin-left: 8px;
    font-size: 14px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
  }
}

.pool_title {
  font-size: 14px;
  font-weight: 600;
  color: $color-text-default;
  line-height: 20px;
  margin-left: 16px;
}

.m_apy_card {
  background: $color-info-card-bg;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 10px;
  &:last-child {
    margin-bottom: 0;
  }
  &_time {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-7;
    line-height: 17px;
  }
  &_title_border {
    padding-bottom: 12px;
    margin: 12px 0;
    border-bottom: 1px solid $color-text-default-06;
  }
  &_item {
    margin: 14px 0;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &_item_my_vote {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &_label {
    font-size: 12px;
    font-family: PingFangSC-Regular, PingFang SC;
    font-weight: 400;
    color: $color-text-default-5;
    line-height: 17px;
  }
  &_value {
    font-size: 14px;
    font-weight: 600;
    color: $color-text-default;
    line-height: 20px;
  }
}
