@import '../../assets/css/theme';
$primary-btn-color: #722fff;
$primary-btn-hover-color: #722fff;

.sun-button-primary {
  background: $color-button-primary;
  border-color: $color-button-primary;
  color: #fff;
  border-radius: 4px;
  height: 29px;
  width: 60px;
  line-height: 29px;
  text-align: center;
  cursor: pointer;
  font-size: 12px;
  &:hover {
    background: $color-button-primary;
    border-color: $color-button-primary;
  }
  &:disabled,
  &.sun-button-weak {
    background: $color-text-default-3;
    border-color: $color-text-default-3;
    color: rgba(#fff, 0.5);
    cursor: no-drop;

    &:hover {
      background: $color-text-default-3;
      border-color: $color-text-default-3;
      color: rgba(#fff, 0.5);
    }
  }
}
